const Buttons = () => <>
    <Button>Tlaidlo</Button>
    <Button>Tlaidlo</Button>
    <Button>Tlaidlo</Button>
    <Button>Tlaidlo</Button>

    <div className={classes.br}></div>

    <Button IconTop={<IconSearch/>} IconLeft={<IconSearch/>}>
        Hada
    </Button>
    <Button IconTop={<IconSearch/>} IconRight={<IconSearch/>}>
        Hada
    </Button>
    <Button IconTop={<IconSearch/>}>
        Hada
    </Button>
    <Button IconLeft={<IconSearch/>} />

    <div className={classes.br}></div>

    <Button IconLeft={<IconSearch/>} outlined>
        Hada
    </Button>
    <Button IconLeft={<IconSearch/>} outlined disabled>
        Hada
    </Button>
    <Button IconLeft={<IconSearch/>} outlined dangerous>
        Hada
    </Button>
    <Button IconLeft={<IconSearch/>} outlined dangerous disabled>
        Hada
    </Button>

    <div className={classes.br}></div>

    <Button IconRight={<IconSearch/>}>
        Hada
    </Button>
    <Button IconRight={<IconSearch/>} disabled >
        Hada
    </Button>
    <Button IconRight={<IconSearch/>} dangerous >
        Hada
    </Button>
    <Button IconRight={<IconSearch/>} disabled dangerous>
        Hada
    </Button>

    <div className={classes.br}></div>

    <div className={classes.compOutlined}>
        <div className={classes.comp}>{"<<komponent>>"}</div>
    </div>

    <div className={classes.compOutlinedBlock}>
        <div className={classes.comp}>{"<<komponent>>"}</div>
    </div>
</>

const SegButtons = () => <>
    <SegmentedButtonLabeled
        label="oznaenie"
        list={["Monos 1", "Monos 2"]}
        value="Monos 1"
    />
    <SegmentedButtonLabeled
        label="oznaenie"
        list={["Monos 1", "Monos 2"]}
        value="Monos 2"
    />

    <div className={classes.br}></div>

    <SegmentedButtonLabeled
        label="Psmen"
        list={["Abc", "Def", "ghi", "jkl", "mn"]}
        helperText="Abecedn zoradenie"
        disabledList={["Def", "mn"]}
    />

    <SegmentedButtonLabeled
        label="Psmen"
        list={["Abc", "Def", "ghi", "jkl", "mn"]}
        helperText="Abecedn zoradenie"
        value="ghi"
        disabledList={["Def", "mn"]}
    />
</> 

const Selects = () => <>
    <SelectLabeled
        label="oznaenie"
        list={["Monos 1", "Monos 2"]}
        value="Monos 1"
    />
    <SelectLabeled
        label="oznaenie"
        list={["Monos 1", "Monos 2"]}
        value="Monos 1"
        forceShow={true}
    />
    <SelectLabeled
        label="oznaenie"
        list={["Monos 1", "Monos 2"]}
        value="Monos 1"
        forceShow={true}
    />
    <SelectLabeled
        label="oznaenie"
        list={["Monos 1", "Monos 2"]}
        value="Monos 2"
        forceShow={true}
    />

    <div className={classes.br}></div>

    <div className={classes.shifted}>
        <SelectLabeled
            label="Psmen"
            list={["Abc", "Def", "ghi", "jkl", "mn"]}
            helperText="Abecedn zoradenie"
            allowBlank
        />

        <SelectLabeled
            label="Psmen"
            list={["Abc", "Def", "ghi", "jkl", "mn"]}
            helperText="Abecedn zoradenie"
            allowBlank
            forceShow
        />
        <SelectLabeled
            label="Psmen"
            list={["Abc", "Def", "ghi", "jkl", "mn"]}
            helperText="Abecedn zoradenie"
            allowBlank
            forceShow
            value="Def"
        />
        <SelectLabeled
            label="Psmen"
            list={["Abc", "Def", "ghi", "jkl", "mn"]}
            helperText="Abecedn zoradenie"
            allowBlank
            value="Def"
            disabled
        />
    </div>
</>

function NotFound() {
    const language = useLanguage()

    return (
        <div className={classes.container}>
            <Selects/>
        </div>
    )
}

export default NotFound